        this.scroll = this.$el.getBoundingClientRect().bottom + window.pageYOffset;
        this.innerHTML = '\u753B\u9762\u30B5\u30A4\u30BA\uFF1A\n                          w: ' + this.width + 'px\n                          h: ' + this.height + 'px\n                          s: ' + this.scroll + 'px';
